import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/company/mccoy-electric-qr-code-uvhu9jk1ia',
    redirect: '/company/McCoy_Electric'
  },
  {
    path: '/company/frahler-electric-company-pvjaxudmry',
    redirect: '/company/Frahler_Electric_Company'
  },
  {
    path: '/company/:companyName',
    name: 'Company Page',
    component: () => import('../views/Company.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
